.jumbotron {
    background:url(/remember-that-character/static/media/cropedTreeHouse.56ed1cf0.jpg);
    /*setup for centering text below; using flexbox 'justify-content' and 'align-items'*/
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:0;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top{
    font-size: 4rem;
    color:#A62722;
    text-shadow: 1px 1px 1px #D3D3D3,
    3px 3px 8px white;
    font-weight: 600;
    font-family: 'Helvetica';
    text-align: center;
}

.instructions{
    font-size: 2rem;
    color:white;
    text-shadow: 1px 1px 1px rgb(87, 79, 79),
    4px 4px 8px black;
    font-weight: 500;
    text-align: center
}
.card{
    margin: .3rem;
    border: 2px solid black;
}
.wrapper {
  background: #ECEFF1;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
  overflow: auto;
  width: 100%;
  height: 100%;
  padding-top: 1rem;
}



.Game{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin:0;
    position: relative;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

